.sub-header {
    height: 280px;
    width: 100%;
    margin-top: -67px;
    border-bottom: 9px solid #A74040;
    h1 {
        color: $gold-color;
        background: none;
        margin-left: 70px;
        margin-top: 82px;
        font-size: 50px;
        line-height: 48px; } }

.homepage {
    .sub-header {
        background: url('../img/backgrounds/subheader-bg.jpg') center top no-repeat; } }

.content {
    color: $grey-color;
    .container {
        border-left: dotted #AAAAAA 1px;
        border-right: dotted #AAAAAA 1px;
        box-shadow: 0 0 50px -2px rgba(0, 0, 0, 0.3);
        padding: 0;
        background-color: #fff; }
    a {
        text-decoration: underline; } }

.article-heading {
	margin: 30px 0 20px 0px;
	font-size: 34px; }

.intro {
    padding-left: 73px;
    padding-right: 15px;
    iframe {
        margin-top: 20px; } }

.video-container {
    position: relative;
    padding-bottom: 56.25%;
    padding-top: 30px;
    height: 0;
    overflow: hidden; }

.video-container iframe,
.video-container object,
.video-container embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

.article-content {
	margin: 0 0 10px 30px;
	padding: 0 0 2px 15px;
	border-left: 1px solid #A74040 {
    font-size: 14px;
    line-height: 18px; } }

.primary {
    h3 {
        background: none;
        margin: 10px 0 30px 0px;
        padding: 30px 0 30px 73px;
        font-size: 26px;
        font-weight: 500;
        border-bottom: solid 3px #A74040;
        background: #f1f1f1; }
    .full-image-slice {
        background: #f1f1f1;
        padding: 20px 20px 20px 73px;
        margin: 36px 0 0 0px;
        img {
            max-width: 100%; } }
    .article-info-wrapper {
        padding-left: 73px;
        padding-right: 15px;
        margin: 10px 0 0 0; }

    .article-section-info {
        font-size: 14px;
        text-indent: 50px;
        padding-bottom: 16px;
        background: url('../img/p-article-styling.png') no-repeat;
        border-bottom: 1px solid #D5C89F; } }


aside {
    box-shadow: inset 3px 0px 3px -1px rgba(217,217,217,1);
    background: linear-gradient(to bottom, rgba(248,248,248,1) 0%, rgba(244,244,244,1) 100%);
    padding: 0 0 0 2px;
    height: 100%;
    overflow: hidden;
    h2 {
        padding: 0 0 15px 0px;
        margin: 30px 0 20px 23px;
        font-size: 34px;
        font-weight: normal; } }

.newest-title {
    background: #EEEEEE url('../img/backgrounds/arrow-link.png') 19px 19px no-repeat;
    padding: 18px 23px;
    text-indent: 15px;
    margin: 20px 0 20px 0px;
    border-bottom: 5px solid #dcdcdc;
    position: relative;
    .aside-arrow {
        position: absolute;
        display: block;
        width: 30px;
        height: 18px;
        background: url('../img/aside-arrow.png') no-repeat;
        bottom: -18px;
        left: 50%;
        transform: translateX(-50%); } }
.newest-image {
    text-align: center; }
